.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.product-cart {
  zoom: 1;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.product-cart:before,
.product-cart:after {
  content: "";
  display: table;
}
.product-cart:after {
  clear: both;
}
.product-cart .cart {
  padding: 1.5em;
  overflow-y: auto ;
  max-height: calc(100% - 180px);
  list-style: none;
  margin: 0;
  width: 100%;
  flex: calc(100% - 180px) 1;
}
.product-cart .cart .item-wrap {
  background-color: #fafafa;
  border-radius: 4px;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  padding: 0.75em;
  zoom: 1;
  position: relative;
  width: 100%;
  margin-bottom: 1em;
  -webkit-animation: an 200ms;
          animation: an 200ms;
}
.product-cart .cart .item-wrap:before,
.product-cart .cart .item-wrap:after {
  content: "";
  display: table;
}
.product-cart .cart .item-wrap:after {
  clear: both;
}
@-webkit-keyframes an {
  from {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes an {
  from {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.product-cart .cart .item-wrap .item {
  display: flex;
  width: 100%;
  align-items: center;
}
.product-cart .cart .item-wrap .item > * {
  flex: 1 1;
}
.product-cart .cart .item-wrap .item > *.media {
  flex: 1 1 60px;
  overflow: hidden;
}
.product-cart .cart .item-wrap .item > *.media img {
  width: 100%;
  height: auto;
}
.product-cart .cart .item-wrap .item > *.data {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: calc(100% - 100px);
  padding: 0 1.5em;
  width: calc(100% - 100px);
}
.product-cart .cart .item-wrap .item > *.data p {
  margin-bottom: 0;
  margin-top: 0;
}
.product-cart .cart .item-wrap .item > *.data p:first-child b {
  font-weight: 700;
  color: #000000;
  display: inline-block;
  margin-right: 0.5em;
  max-width: calc(100% - 40px);
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 0.85em;
}
.product-cart .cart .item-wrap .item > *.data p:first-child .of-tg {
  max-width: 40px;
  display: inline-block;
  padding: 5px;
  background-color: #f8192c;
  color: #ffffff;
  border-radius: 1em;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  font-weight: 700;
  font-size: 0.75em;
}
.product-cart .cart .item-wrap .item > *.data p:last-child {
  display: block;
}
.product-cart .cart .item-wrap .item > *.data p:last-child > * {
  display: inline-block;
  margin-right: 0.5em;
}
.product-cart .cart .item-wrap .item > *.data p:last-child del {
  -webkit-opacity: 0.5;
  -moz-opacity: 0.5;
  opacity: 0.5;
  display: block;
  margin-bottom: 5px;
  font-size: 0.8em;
}
.product-cart .cart .item-wrap .item > *.data p:last-child span {
  color: #161719;
  font-size: 0.85em;
}
.product-cart .cart .item-wrap .item > *.data p:last-child span > span {
  font-weight: 700;
}
.product-cart .cart .item-wrap .item > *.act {
  flex: 1 1 40px;
  text-align: center;
}
.product-cart .cart .item-wrap .item > *.act svg path {
  fill: #cccccc;
  -webkit-transition: all 200ms ease-in-out;
  -moz-transition: all 200ms ease-in-out;
  -ms-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
}
.product-cart .cart .item-wrap .item > *.act:hover {
  cursor: pointer;
}
.product-cart .cart .item-wrap .item > *.act:hover svg path {
  fill: #f8192c;
}
.product-cart .cart-dtl {
  flex: 100px 1;
  width: 100%;
  padding: 1.5em;
  border-top: 1px solid rgba(22, 23, 25, 0.1);
}
.product-cart .cart-dtl .clearfix {
  position: relative;
  width: 100%;
}
.product-cart .cart-dtl .tl {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  padding: 1em 0;
}
.product-cart .cart-dtl .tl.ttl:first-child {
  color: #000000;
  font-weight: 900;
}
.product-cart .cart-dtl .tl.ttl:last-child {
  font-weight: 700;
  color: #000000;
}
.product-cart .cart-dtl .tl > * {
  flex: 1 1 50%;
  position: relative;
}
.product-cart .cart-dtl .tl > *:first-child:after {
  top: 0;
  content: ':';
  display: block;
  position: absolute;
  z-index: 0;
  color: #000000;
  left: auto;
  right: 0;
}
.product-cart .cart-dtl .tl > *:last-child {
  text-align: right;
}
.product-cart .cart-act {
  width: 100%;
  zoom: 1;
  position: relative;
  flex: 42px 1;
}
.product-cart .cart-act:before,
.product-cart .cart-act:after {
  content: "";
  display: table;
}
.product-cart .cart-act:after {
  clear: both;
}
.product-cart .cart-act .cart-btn {
  display: block;
  width: 100%;
  height: 100%;
  outline: none;
  border: 0 !important;
  color: #ffffff;
  background-color: #f8192c;
  border-color: #f8192c;
  box-shadow: 0 0 0 transparent;
  border: 1px solid #f8192c;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
}
.product-cart .cart-act .cart-btn:disabled {
  opacity: 0.5;
  background-color: #161719;
}
.product-cart .cart-act .cart-btn:disabled:hover,
.product-cart .cart-act .cart-btn:disabled:focus {
  background-color: #161719 !important;
  cursor: not-allowed;
}
.product-cart .cart-act .cart-btn:focus,
.product-cart .cart-act .cart-btn.focus {
  outline: none;
  color: #ffffff;
  background-color: #d70718;
  border-color: #8d0410;
}
.product-cart .cart-act .cart-btn:hover {
  outline: none;
  color: #ffffff;
  background-color: #d70718;
  border-color: #ce0617;
}
.product-cart .cart-act .cart-btn:active,
.product-cart .cart-act .cart-btn.active,
.open > .dropdown-toggle.product-cart .cart-act .cart-btn {
  color: #ffffff;
  background-color: #d70718;
  border-color: #ce0617;
}
.product-cart .cart-act .cart-btn:active:hover,
.product-cart .cart-act .cart-btn.active:hover,
.open > .dropdown-toggle.product-cart .cart-act .cart-btn:hover,
.product-cart .cart-act .cart-btn:active:focus,
.product-cart .cart-act .cart-btn.active:focus,
.open > .dropdown-toggle.product-cart .cart-act .cart-btn:focus,
.product-cart .cart-act .cart-btn:active.focus,
.product-cart .cart-act .cart-btn.active.focus,
.open > .dropdown-toggle.product-cart .cart-act .cart-btn.focus {
  color: #ffffff;
  background-color: #b50614;
  border-color: #8d0410;
  outline: none;
}
.product-cart .cart-act .cart-btn:active,
.product-cart .cart-act .cart-btn.active,
.open > .dropdown-toggle.product-cart .cart-act .cart-btn {
  background-image: none;
}
.product-cart .cart-act .cart-btn.disabled:hover,
.product-cart .cart-act .cart-btn[disabled]:hover,
fieldset[disabled] .product-cart .cart-act .cart-btn:hover,
.product-cart .cart-act .cart-btn.disabled:focus,
.product-cart .cart-act .cart-btn[disabled]:focus,
fieldset[disabled] .product-cart .cart-act .cart-btn:focus,
.product-cart .cart-act .cart-btn.disabled.focus,
.product-cart .cart-act .cart-btn[disabled].focus,
fieldset[disabled] .product-cart .cart-act .cart-btn.focus {
  background-color: #f8192c;
  border-color: #f8192c;
  outline: none;
}
.product-cart .cart-act .cart-btn .badge {
  color: #f8192c;
  background-color: #ffffff;
}
.product-cart .empty-cart {
  height: 100%;
  width: 100%;
  position: relative;
  padding: 3em;
  text-align: center;
}
.product-cart .empty-cart img {
  margin: auto;
  max-width: 100%;
}
